.detail_contac__b1uKU{
    
    -webkit-transition:all .3s;
    transition:all .3s;
    text-align: center;

}
.detail_contac__b1uKU:after{
    content:"";
    left:-35%;
    top:0;
    -webkit-transform:skew(50deg);
    transform:skew(50deg);
    -webkit-transition-duration:.6s;
    transition-duration:.6s;
    -webkit-transform-origin:top left;
    transform-origin:top left;    
    -webkit-transition:all .3s;
    transition:all .3s
}
.detail_contac__b1uKU:hover:after{
    height:100%;
    width:135%;
    cursor:pointer;
}

